import './polyfill.js'

import * as props from './svg.js'
import * as ids from './ids.js'

export default function SVG (el) {
  if (props.supported) {
    let element = new Doc(el)

    if (!props.parser)
      props.prepare(element)

    return element
  }
}
for (let i in props) {
  SVG[i] = props[i]
}
SVG.eid = ids.eid
Object.defineProperty(SVG, 'did', {
  get: ids.getDid
})

// import classes
import Element from './element.js'
import './arrange.js'
import './attr.js'
import './data.js'
import './helpers.js'
import './memory.js'
import './style.js'
import './sugar.js'
import * as transforms from './transform.js'
SVG.Element = Element

for(let i in transforms)
	SVG[i] = transforms[i]

import svg_Array from './array.js'
SVG.Array = svg_Array

import bare from './bare.js'
SVG.Bare = bare

import {BBox, TBox, RBox} from './boxes.js'
SVG.BBox = BBox
SVG.RBox = RBox
SVG.TBox = TBox

import ClipPath from './clip.js'
SVG.ClipPath = ClipPath

import Color from './color.js'
SVG.Color = Color

import Container from './container.js'
SVG.Container = Container

import defaults from './default.js'
SVG.defaults = defaults

import Defs from './defs.js'
SVG.Defs = Defs

import './helpers.js'
import Doc from './doc.js'
SVG.Doc = Doc

import {Circle, Ellipse} from './ellipse.js'
SVG.Circle = Circle
SVG.Ellipse = Ellipse

import {on, off, listeners, handlerMap, listenerId} from './event.js'
SVG.on = on
SVG.off = off
SVG.listeners = listeners
SVG.handlerMap = handlerMap
SVG.listenerId = listenerId

import FX, {easing, Situation, Delay, MorphObj, morph} from './fx.js'
SVG.FX = FX
SVG.easing = easing
SVG.Situation = Situation
SVG.Delay = Delay
SVG.MorphObj = MorphObj
SVG.morph = morph

import Gradient, {Stop} from './gradient.js'
SVG.Gradient = Gradient
SVG.Stop = Stop

import Group from './group.js'
SVG.G = Group
SVG.Group = Group

import A from './hyperlink.js'
SVG.A = A

import svg_Image from './image.js'
SVG.Image = svg_Image

import Line from './line.js'
SVG.Line = Line

import Marker from './marker.js'
SVG.Marker = Marker

import Mask from './mask.js'
SVG.Mask = Mask

import Matrix from './matrix.js'
SVG.Matrix = Matrix

import Nested from './nested.js'
SVG.Nested = Nested

import svg_Number from './number.js'
SVG.Number = svg_Number

import Parent from './parent.js'
import './ungroup.js'
SVG.Parent = Parent

import Path from './path.js'
SVG.Path = Path

import PathArray from './patharray.js'
SVG.PathArray = PathArray

import Pattern from './pattern.js'
SVG.Pattern = Pattern

import Point from './point.js'
SVG.Point = Point

import PointArray from './pointarray.js'
SVG.PointArray = PointArray

import {Polyline, Polygon} from './poly.js'
import './pointed.js'
SVG.Polyline = Polyline
SVG.Polygon = Polygon

import Rect from './rect.js'
SVG.Rect = Rect

import regex from './regex.js'
SVG.regex = regex

import {get, select} from './selector.js'
SVG.get = get
SVG.select = select

import svg_Set from './set.js'
SVG.Set = svg_Set

import Shape from './shape.js'
SVG.Shape = Shape

import Text from './text.js'
SVG.Text = Text

import TextPath from './textpath.js'
SVG.TextPath = TextPath

import Use from './use.js'
SVG.Use = Use

import utils from './utilities.js'
SVG.utils = utils

import ViewBox from './viewbox.js'
SVG.ViewBox = ViewBox

// tell all the modules that the library has loaded
require('./circularReferenceFix.js').loaded();

// export the function and not a object
if (module && module.exports)
  module.exports = module.exports.default



